:root {
    --primary: #007aff;
    --root: #fff;
}

* {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", sans-serif;
    box-sizing: border-box;
    font-weight: normal;
}

::selection, ::-webkit-selection { background-color: rgba(0,123,255,.25); }

body {
    margin:0;
    background-color:#f9fafc;
}

#root {
    width:100%;
    height:100vh;
    display:block;
    flex-direction: row;
    padding-top:calc(3.75vh + 25px);
}

#navbar-title {
    font-size: 21px;
    color:#888;
    text-align: center;
    margin-left: 10px;
}

#navbar {
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width:450px;
    height:58px;
    padding:5px 10px;
    border-radius: 8px;
    background-color:var(--root);
    box-shadow:0 5px 15px rgba(0,0,0,.0325);
    position:fixed;
    z-index:3000;
    top:calc(1.5vh + 25px);
    left: calc(50% - 225px);
    transition: box-shadow .25s ease-out;
    animation: nav-in .425s ease-out 0s 1;
}

@keyframes nav-in {
    from { opacity:0; transform: translateY(-10px); }
}

#navbar:hover, #navbar:focus {
    box-shadow:0 5px 15px rgba(0,0,0,.0625);
}

#navbar-upload {
    padding:5px 15px;
    text-transform: uppercase;
    color:var(--primary);
    font-size: 18px;
    appearance: none;
    -webkit-appearance: none;
    border:0;
    background-color: transparent;
    cursor:pointer;
    outline:none;
    border-radius: 5px;
    transition: background-color .25s ease-out;
}

#navbar-upload:hover, #navbar-upload:focus { background-color: rgba(0,123,255,.0875); }
#navbar-upload:active { background-color: rgba(0,123,255,.1075); }

#card {
    flex-direction: column;
    background-color:var(--root);
    width:550px;
    padding:0;
    padding-bottom: 10px;
    margin:calc(5vh + 35px) auto 30px auto;
    box-shadow:0 20px 40px rgba(0,0,0,.125);
    border-radius: 5px;
    animation: card-in .5s ease-out 0s 1;
    transition: box-shadow .325s ease-out;
}

#card:hover, #card:focus {
    box-shadow:0 20px 40px rgba(0,0,0,.2);
}

@keyframes card-in {
    from { opacity:0; transform: translateY(20px); box-shadow: 0; }
}

#card-image {
    display:block;
    width:100%;
    min-height:50px;
    transition:height .25s ease-out;
    object-fit: cover;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

#card-title {
    width:100%;
    display:block;
    padding:15px 0 10px 0;
    margin:0;
    font-size: 24px;
    font-weight: normal;
    color:var(--primary);
    margin:0 0 2.5px 0;
}

#card-detail {
    width:100%;
    display:block;
    color:#aaa;
    font-size: 16px;
    padding:0;
    margin:0 0 10px 0;
}

#card-body {
    width:100%;
    display:flex;
    flex-direction: row;
    align-items: center;
}

#card-progress-wrapper {
    padding:0 20px;
}

#card-progress {
    width:50px;
    height:50px;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
}

#card-main {
    flex:1;
}

h1, p { cursor:default; }

#card-upload {
    display:flex;
    padding:25px;
    width:400px;
    height:300px;
    position:absolute;
    margin-top:85px;
    left: calc(50% - 200px);
    background-color:var(--root);
    border-radius: 5px;
    box-shadow:0 5px 15px rgba(0,0,0,.0625);
    transition:box-shadow .25s ease-out;
    align-items: center;
    justify-content: center;
    font-size: 48px;
    color:var(--primary);
    font-weight: 300;
    letter-spacing: .25rem;
    cursor:pointer;
    animation: card-in .5s ease-out 0s 1;
}

#card-upload:hover, #card-upload:focus {
    box-shadow:0 8px 25px rgba(0,0,0,.0875);
}

.hidden { display:none !important; }

#loadscreen {
    display:flex;
    width:100%;
    height:calc(100vh - 58px);
    position:fixed;
    z-index:4000;
    top:58px;
    left:0;
    padding-bottom:58px;
    background-color:var(--root-2);
    animation: load-in .25s ease-out 0s 1;
}

@keyframes load-in {
    from {opacity:0;}
}

.loader {
    position: relative;
    margin: 0 auto;
    width: 100px;
}

.loader:before {
    content: '';
    display: block;
    padding-top: 100%;
}
  
.circular {
    animation: rotate 2s linear infinite;
    height: 100%;
    transform-origin: center center;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

.circular-shadow {
  top:5px !important;
  left:5px !important;
  opacity:.25;
  filter: blur(5px);
}
  
.path {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    animation: dash 1.5s ease-in-out infinite;
    stroke-linecap: round;
    stroke: var(--primary);
}

.path-shadow {
    stroke: var(--primary-2) !important;
    opacity:.5;
}

#reload-button {
    padding: 5px 15px;
    border-radius: 3px;
    font-size: 16px;
    color:var(--primary);
    text-transform: uppercase;
    width:100%;
    position:absolute;
    text-align: center;
    bottom: calc(22.5% - 50px);
    cursor:pointer;
    border:1px solid var(--primary);
    transition: background-color .25s ease-out;
    animation: reload-in 5s ease-out 0s 1;
}

@keyframes reload-in {
  0% {opacity:0;}
  92.5% {opacity:0;}
  100% {opacity:1;}
}

@keyframes rotate {
    100% {
      transform: rotate(360deg);
    }
}
  
@keyframes dash {
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -35px;
    }
    100% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -124px;
    }
}

@media screen and (max-width: 568px){
    #navbar {
        width:90%;
        left:5%;
    }
    #card-upload {
        width:90%;
        left:5%;
    }
    #card {
        width:90%;
        left:5%;
    }
}